<template>
	<div>
		<HeaderComponent :view-title="vipTitle"></HeaderComponent>
		<div id="main-container" v-if="isShowSvg == 'true'">
			<div class="vipSvg">
				<svg class="iconVip" aria-hidden="true">
			  		<use xlink:href="#icon-huiyuan"></use>
				</svg>
				<p>会员权益暂未开放</p>
			</div>
			<div class="bottomText">
				<p>现在可以免费发布抽奖活动</p>
				<p>
					<router-link to="/activity" class="redText">快去创建吧></router-link>
				</p>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		vipTitle:Object,
		isShowSvg:Boolean,
		data(){
			return{
				vipTitle:{
					name:'会员',
					showCode:1
				},
				isShowSvg:'true'
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconVip {
	   width: 6rem; height: 6rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#D4D4D4;
	}
	#main-container{
		margin-top:2.2rem;
		.vipSvg{
			text-align: center;
			@include center;
			p{
				margin-top:0.5rem;
				@include sc(0.8rem,#d4d4d4);
			}
		}
		.bottomText{
			width:100%;
			text-align:center;
			@include cl;
			bottom:2rem;
			p:first-child{
				@include sc(0.8rem,#838383);
				margin-bottom:0.5rem;
			}
			p:last-child{
				.redText{
					@include sc(0.8rem,#F45757);
					border-bottom:1px solid #F45757;
				}
			}
		}
	}
</style>


